.modal {
  position: fixed;
  top: 83px;
  right: 0;
  bottom: 0;
  left: -18px;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  .btn {
    width: 100%;
    margin-top: 23px;
  }
}

.modal__wrapper {
  position: relative;

  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;

  @include vp-767 {
    padding: 60px 16px;
  }
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  cursor: pointer;
}

.modal__content {
  position: relative;

  width: 500px;
  padding: 40px;

  background-color: $color-blue;
  color: $color-default-white;

  @include vp-767 {
    width: 100%;
    padding: 20px;
  }
}

.modal__title {
  font-family: $font-Roboto;
  font-weight: 400;
  font-size: 30px;
  line-height: 42.19px;
  margin: 0 0 2px;
}

.modal__description {
  margin: 0 0 40px;
}


.modal__close-btn {
  position: absolute;
  top: 25px;
  right: 25px;
  z-index: 1;

  width: 20px;
  height: 20px;
  padding: 0;

  background: $color-transparent;
  border: none;
  cursor: pointer;

  svg {
    fill: $color-default-white;
  }
}

.modal--responsive {
  .modal__content {
    width: 100%;
  }
}

.modal--fit-content {
  .modal__content {
    width: auto;

    @include vp-767 {
      width: 100%;
    }
  }
}

.modal--no-scale {
  transform: none;
}

.modal--preload {
  transition: none;
}

.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;

  pointer-events: auto;
}

